import * as React from 'react';
import './WindowComponent.css';
import * as moment from 'moment';

class WindowComponent extends React.Component {
    getCurrentTime() {
        return moment().format('ddd MMM DD HH:mm:ss');
    }

    render() {
        return (
            <div className="ozzy-WindowComponent">
                <div className="ozzy-WindowHeader">
                    <div className="ozzy-WindowButtons">
                        <div className="redButton" />
                        <div className="yellowButton" />
                        <div className="greenButton" />
                    </div>
                    <div className="ozzy-WindowTitle">guest@ozguncagri.com: ~ (zsh)</div>
                </div>
                <div className="ozzy-WindowContent">
                    <div className="textLine tn-white">Last login: <span>{this.getCurrentTime()}</span> on ttys001</div>
                    <div className="textLine">
                        guest
                        <span className="tb-magenta">@</span>
                        ozguncagri.com
                        <span className="tb-green tilda">~</span>pandoc -f markdown -t html info.md
                    </div>
                    <p>Hi,</p>
                    <p>My name is Özgün and I'm yet another software developer who struggles with various web
                        technologies.</p>
                    <p>BTW if you want, you can check <a href="http://ozguncagri.com/blog">my blog</a> (Turkish)...</p>
                    <div className="textLine">guest<span className="tb-magenta">@</span>
                        ozguncagri.com
                        <span className="tb-green tilda">~</span>
                        <div className="blink_me" />
                    </div>
                </div>
            </div>
        );
    }
}

export default WindowComponent;


